.container {
  max-width: unset;
}
body {
  font-size: 18px;
}

/* Set theme color */
.btn,
.btn-floating {
  background-color: #1565c0;
}
.page-footer,
.nav-wrapper,
.statusbar,
.nav-content {
  background-color: white;
