.markdown ul > li {
  padding-left: 20px;
  list-style-type: disc;
}
.leaflet-top,
.leaflet-bottom {
  z-index: 950;
}

/* styles for '...' */
.block-with-text {
  /* hide text if it more than N lines  */
  overflow: hidden;
  /* for set '...' in absolute position */
  position: relative;
  /* use this value to count block height */
  line-height: 1.2em;
  /* max-height = line-height (1.2) * lines max number (3) */
  max-height: 3.6em;
  /* fix problem when last visible word doesn't adjoin right side  */
  text-align: justify;

  /* */
  margin-right: -1em;
  padding-right: 1em;
}
.block-with-text:before {
  /* points in the end */
  content: '...';
  /* absolute position */
  position: absolute;
  /* set position to right bottom corner of block */
  right: 0;
  bottom: 0;
}
.block-with-text:after {
  /* points in the end */
  content: '';
  /* absolute position */
  position: absolute;
  /* set position to right bottom corner of text */
  right: 0;
  width: 1em;
  /* set width and height */
  height: 1em;
  margin-top: 0.2em;
  background: white;
}
.sidenav {
  top: 66px;
}
.buttons a {
  margin: 5px;
}

/* print styles */
@media print {
  html,
  body {
    font-size: 14px;
  }
  body {
    margin: 0;
    color: #000;
    background-color: #fff;
  }
  a[href^='http']::after {
    content: ' (' attr(href) ')';
  }
  .leaflet-container,
  img,
  pre,
  blockquote {
    page-break-inside: avoid;
    page-break-before: auto; /* 'always,' 'avoid,' 'left,' 'inherit,' or 'right' */
    page-break-after: auto; /* 'always,' 'avoid,' 'left,' 'inherit,' or 'right' */
    page-break-inside: avoid; /* or 'auto' */
  }
  main::after {
    content: 'Copyright DRIVER+';
    display: block;
    text-align: center;
  }
  .do-not-print,
  header,
  footer,
  aside,
  nav,
  form,
  iframe,
  .menu,
  .hero,
  .adslot {
    display: none;
  }
}

